
.user {
	display: flex;
	margin-bottom: 20px;
	position: relative;
}
.titleName {
	font-weight: 500;
	font-size: 26px;
	line-height: 35px;
	color: #22577A;
	margin-bottom: 15px;
}


.about {
	font-style: normal;
	font-weight: 400;
	font-size: 16px;
	line-height: 22px;
	color: #767676;
	height: auto;
}
.status {
	position: relative;
	background: #F3FFF6;
	border-radius: 30px;
	padding: 5px 10px;
	margin-bottom: 15px;
}


.status::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 20px;
    border-bottom: 20px solid #F3FFF6;
    border-top-color: #F3FFF6;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
}

.editStatusBtn {
	position: absolute;
	top: 0;
	right: 10px;
	box-shadow: 2px 2px 4px 2px rgba(0, 0, 0, 0.1);
	border-radius: 50%;
	padding: 15px 18px;
	
}
.redactTA {
	border-radius: 20px;
	border: 1px solid black;
	padding: 5px 0 5px 5px;
}
.contacts {
	display: flex;
}
.contacts li:not(:last-child) {
	margin-right: 10px;
}
.contactsEdit {
	display: flex;
	flex-direction: column;
}
textarea {
	resize: none;
}
.avatarAbout {
	display: flex;
	flex-direction: column;
	height: 100%;
}
.redactAboutMe {
	border-radius: 20px;
	border: 1px solid black;
	padding: 5px 0 5px 5px;
	max-width: 130px;
}
.aboutMe {
	max-width: 130px;
}