@import "@font/nunito";
.loginWrapper {
	width: 100vw;

	overflow: hidden;
}
.wrapper {
	height: calc(100vh - 42px);
	background: transparent url(../../img/loginBG.png) center center / cover;
	display: flex;
	align-items: center;
	justify-content: center;
	font-family: var(--fontFamily-nunito), sans-serif;
}

.input {
	border: 1px solid #38A3A5;
	border-radius: 30px;
	color: #000000;
	margin-top: 8px;
padding: 19px 0 19px 40px;
transition: border .2s ease-in-out, background-color .2s ease-in-out;
}
.input::placeholder {
	font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 22px;
color: #A4A4A4;
}

.input:focus {
	border: 1px solid #000000;
}
.input:hover {
	background-color: #F2F2F2;
}
.input:active {
	border: 1px solid #38A3A5;
}
.formWrapper {
	display: flex;
	flex-direction: column;
	border-radius: 30px;
	overflow: hidden;
	box-shadow: 2px 2px 4px 2px rgba(0, 0, 0, 0.1);
}

.logo {
	background-color: #A5E4C9;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 50px 110px 25px;
}
.form {
	display: flex;
	flex-direction: column;
	background-color: white;
	align-items: center;
	padding: 25px 60px;
}
.titleForm {
	font-style: normal;
	font-weight: 400;
	font-size: 26px;
	line-height: 35px;
	color: #000000;
	margin-bottom: 25px;
}
.label {
	display: flex;
	flex-direction: column;
	font-style: normal;
font-size: 16px;
line-height: 22px;
color: #767676;
margin-bottom: 22px;
width: 100%;
font-weight: var(--fontWeight-nunito-bold);
}
.fakeCheck {
	position: relative;
}
.checkBox {
	position: absolute;
	top: 0;
	left: -30px;
	z-index: -1;
	opacity: 0;
}
.bottomWrapper {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	margin-top: 40px;
}
.checkBox + label {
	cursor: pointer;
	display: inline-flex;
	align-items: center;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 22px;
color: #767676;
}
.checkBox + label::before {
	content: '';
	color: transparent;
	display: inline-block;
	width: 24px;
	height: 24px;
	flex-shrink: 0;
	flex-grow: 0;
	border: 1px solid #22577A;
	border-radius: 5px;
	margin-right: 0.5em;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: 50% 50%;
}

.checkBox:checked + label::before {
	content: '';
	color: white;
	border: 1px solid #22577A;
background-color: #22577A;
background-image: url(../../img/agree.svg);
}
.btn {
	align-self: end;
	background: #22577A;
	box-shadow: 2px 2px 4px 2px rgba(0, 0, 0, 0.1);
	border-radius: 30px;
	padding: 12px 53px;
	color: #fff;
	border: 1px solid transparent;
	transition: background-color .3s ease, border .3s ease, box-shadow .3s ease;
	font-weight: 600;
font-size: 16px;
line-height: 22px;
}
.btn:hover {
	background-color: #38A3A5;
}
.btn:active {
	box-shadow: inset 5px 5px 4px 2px rgba(0, 0, 0, 0.1);
}
.btn:focus {
	border: 1px solid #000000;
}
.btn:disabled {
	background-color: #A4A4A4;
}
.footer {
	padding: 10px;
	font-style: normal;
	font-weight: 400;
	font-size: 16px;
	line-height: 22px;
	color: #767676;
	width: 100vw;
display: flex;
justify-content: space-around;
background: #F4F4F4;
}
.error {
	height: 15px;
	color: red;
}
.incorrect {
	padding-top: 5px;
	height: 15px;
	color: red;
}