.user {
	padding: 8px;
	display: flex;
}
.userLink {
	display: flex;
}
.user:not(:last-child) {
	border-bottom: 1px solid #C7F9CC;
}
.avatar {
	width: 33px;
	height: 33px;
	border-radius: 50%;
	margin-right: 20px;
}
.name {
	font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 22px;

color: #767676;
}