.user {
	display: flex;
}

.userAvatar {
	width: 130px;
	height:130px;
	object-fit:fill;
	margin-right: 40px;
	border-radius: 30px;
}

.fullName {
	font-style: normal;
	font-weight: 500;
	font-size: 26px;
	line-height: 35px;
	color: #22577A;
	margin-bottom: 10px;
}
.aboutUser {
	display: flex;
	flex-direction: column;
}
.status {
	height: 100%;
	width: 200px;
	background: #F5F5F5;
	box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1), inset 1px 1px 4px rgba(255, 255, 255, 0.1);
	border-radius: 30px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.btn {
	width: 212px;
	
	background: #22577A;
	box-shadow: 2px 2px 4px 2px rgba(0, 0, 0, 0.1);
	border-radius: 30px;
	padding: 12px 53px;
	color: #fff;
	border: 1px solid transparent;
	transition: background-color .3s ease, border .3s ease, box-shadow .3s ease;
	font-weight: 600;
font-size: 16px;
line-height: 22px;
}
.btn:hover {
	background-color: #38A3A5;
}
.btn:active {
	box-shadow: inset 5px 5px 4px 2px rgba(0, 0, 0, 0.1);
}
.btn:focus {
	border: 1px solid #000000;
}
.btn:disabled {
	background-color: #A4A4A4;
}
.wrapper {
	padding-right: 10px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 44px;
}