.header {
	padding: 15px 25px ;
	display: flex;
	align-items: center;
	justify-content:space-between;
	background-color: #A5E4C9;
	grid-column: 1 / 13;
	grid-row: 1 / 2;
	border-radius: 0 0 30px 30px;
	height: 100px;
	z-index: 2;
	position: relative;
}

.logo {
	width: 122px;
	height: 45px;
}
.searchWrapper {
	position: relative;
}
.search {
	border-radius: 30px;
	background-color: white;
	position: relative;
	padding: 10px;
}
.search::after {
	content: 'sss';
	position: absolute;
	top: 0;
	left: 0px;
}
.searchList {
	position: absolute;
	top: 100%;
	left: 30px;
	background: #FFFFFF;
box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
border-radius: 30px;
padding: 8px;
}

.profileLink {
	padding: 15px;
}
.avatarSmall {
	width: 50px;
	height: 50px;
	border-radius: 50%;
	overflow: hidden;
}
